import {
    SET_MESSAGE
  } from '../actions/ApplicationActions';
  
  const initialState = {
    message: {
      text: ""
    }
  };
  
  const EcommerceReducer = function (state = initialState, action) {
    switch (action.type) {
      case SET_MESSAGE: {

        return {
          ...state,
          message: {
            text: action?.payload.text
          },
        };
      }
      default: {
        return {
          ...state,
        };
      }
    }
  };
  
  export default EcommerceReducer;
  